import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';

import Img from './Img';
import StyleCommon from './StyleCommon';



export default function DataGridDmgChr(props) {
  const { data,  Calc, list, cNameF, AE } = props.props

  const columns = [
    {
      field: 'No',
      headerName: '',
      width: 50,
      disableColumnMenu: true,//
      align: 'center',
    },

    {
      field: 'Nam',
      headerName: '',
      width: 150,
      disableColumnMenu: true,//

    },

    {
      field: 'Ara',
      headerName: '',
      headerAlign: 'center',
      width: 70,
      align: 'center',
      disableColumnMenu: true,//

      renderCell: (params) => (
        <Img name={params.row.Ara} width={70} />//height={35}
      )
    },
    {
      field: 'Dmg',
      headerName: '',
      headerAlign: 'center',
      align: 'right',
      width: 120,
      disableColumnMenu: true,//

      renderCell: (params) => params.row.DmgTxt
    },

    {
      field: 'DmgBar',
      headerName: '',
      width: 110,
      disableColumnMenu: true,//

      renderCell: (params) => (
        <>
          {AE === 'Ally' ?
            <Img name={'DmgBar'} width={params.row.Dmg / 100} height={100} /> :
            <>
              <Img name={'HPBar'} width={(params.row.ZHP - params.row.Dmg) / 20} height={100} />
              <Img name={'DmgBar'} width={params.row.Dmg / 20} height={100} />
            </>
          }
        </>
      )
    },



    {
      field: 'BasDmgStr',
      headerName: '&(+10)',
      width: 240,
      align: 'left',
      disableColumnMenu: true,//

    },
    {
      field: 'Amp',
      headerName: '',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//

    },
    {
      field: '',
      headerName: '',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//

    },
    {
      field: '',
      headerName: '',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//

    },
    {
      field: '',
      headerName: '',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//

    },


  ];


  const rows = []
  data.C1[cNameF][AE].Skl2.forEach((SklEl, i) => {
    rows.push({
      ...SklEl,
      id: i,
      // No: SklEl.No,
      // Nam: SklEl.Nam,
      // Ara: SklEl.Ara,
      Elm: data.Skl[SklEl.Nam].Elm,
      // Dmg: SklEl.Dmg,
      // DmgTxt: SklEl.DmgTxt,
      DmgBar: SklEl.Dmg,
      ZHP: data.C1[cNameF].Ally.St.Btl.HP,
      // BasDmgStr: SklEl.BasDmgStr,
      // Amp: SklEl.Amp,
      // : SklEl.,
    })


  })



  return (
    <Box sx={{ height: 250, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//
        // autoHeight//
        hideFooter//
        hideFooterSelectedRowCount//
        hideFooterPagination//(

        sx={StyleCommon}
        getRowClassName={(params) => params.row.Elm}
          

      // initialState={{
      //   pagination: {
      //     paginationModel: {
      //       pageSize: 5,
      //     },
      //   },
      // }}
      // pageSizeOptions={[5]}
      // checkboxSelection
      // disableRowSelectionOnClick
      // 

      />
    </Box>
  );
}