import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Unstable_Grid2';


import DataGridDmgChr from "./DataGridDmgChr";

import TabParams from "./TabParams";
import TabAE from "./TabAE";
import RatingAwk from "./RatingAwk";
import ModalEqSlct from "./ModalEqSlct";
import Img from './Img';
import CNSplit from "./CNSplit"
import ModalChrSkl from './ModalChrSkl';
import { TextCNameF } from './Text';


const HTADSIM = ['HP', 'TP', '', '', '', '', '']
const SSBList = ['', '', '']
const ResList = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
const ElList = ['', '', '', '', '', '', '', '']



export default function GridChr(props) {
  const { data, Calc, list, cNameF } = props.props

  let cName1 = CNSplit(cNameF, 1)
  let cName2 = CNSplit(cNameF, 2)

  return (
    <Grid container spacing={1}>
      <Grid xs={6}>
        <Grid container spacing={0}>
          <Grid xs='auto' sx={{ pr: 1 }}>
            <Img name={cNameF + 'i'} width={200} />
          </Grid>
          <Grid xs>
            <TextCNameF text={cNameF} /><br />
            <Img name={data.Chr[cName1][cName2].Gr} width={35} /><Img name={data.Chr[cName1][cName2].Typ} width={35} /><br />
            Lv100EXLv40<RatingAwk props={props.props} />
            {/* <Grid container spacing={1}> */}
          </Grid>
          <Grid xs={12}>

            {/* +{data.C1[cNameF].Ally.Crr}<br />
            {data.C1[cNameF].Ally.Cut.}<br />
            {data.C1[cNameF].Ally.Cut.} */}
            <ModalEqSlct props={{ ...props.props, AE: 'Ally', Eq: 'Wepn' }} C={props.C} />
            <ModalEqSlct props={{ ...props.props, AE: 'Ally', Eq: 'Armr' }} C={props.C} />
            <ModalEqSlct props={{ ...props.props, AE: 'Ally', Eq: 'Bddy' }} C={props.C} />
            <ModalChrSkl props={{ ...props.props, AE: 'Ally' }} />
            {/* </Grid> */}
          </Grid>
        </Grid>

      </Grid>
      <Grid xs={6}>
        <TabAE props={props.props} C={props.C} />
      </Grid>
      <Grid xs={6}>
        <DataGridDmgChr props={{ ...props.props, AE: 'Ally' }} C={props.C} />
      </Grid>
      <Grid xs={6}>
        <DataGridDmgChr props={{ ...props.props, AE: 'Enmy' }} C={props.C} />
      </Grid>

    </Grid>
  );
}